body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.unstocked-item {
  color: var(--red-color-light);
}

.action-link {
  height: 1.5rem;
  width: 1.5rem;
  opacity: 0;
  border-radius: 4px;
  font-weight: bold;
  -webkit-transition: opacity var(--transition-time), color var(--transition-time), background-color var(--transition-time), text-shadow var(--transition-time);
  transition: opacity var(--transition-time), color var(--transition-time), background-color var(--transition-time), text-shadow var(--transition-time);
  outline: none;
}

tr:hover .action-link {
  opacity: 1;
  cursor: pointer;
}

.button-cell {
  position: relative;
}

.red-button {
  border: 1px solid var(--red-color-main);
  background-color: white;
  color: var(--red-color-main);
  text-shadow: 0px 0px white;
}

.green-button {
  border: 1px solid var(--green-color-main);
  background-color: white;
  color: var(--green-color-main);
  text-shadow: 0px 0px white;
}

.red-button:hover {
  color: white;
  background-color: var(--red-color-main);
  text-shadow: -1px -1px var(--red-color-dark);
  text-shadow: 1px 1px var(--red-color-light);
}

.green-button:hover {
  color: white;
  background-color: var(--green-color-main);
  text-shadow: -1px -1px var(--green-color-dark);
  text-shadow: 1px 1px var(--green-color-light);
}

@-webkit-keyframes loading {
  from {
    width: 0%;
    opacity: .2;
    color: white;
  }
  to {
    width: 100%;
    opacity: 1;
    color: rgb(0, 76, 190);
  }
}

@keyframes loading {
  from {
    width: 0%;
    opacity: .2;
    color: white;
  }
  to {
    width: 100%;
    opacity: 1;
    color: rgb(0, 76, 190);
  }
}

.loading-bar {
  height: 1.5rem;
  border-radius: 3px;
  background-color: rgb(0, 76, 190);
  padding-left: 1rem;
  font-weight: bold;
  font-style: italic;
  -webkit-animation-name: loading;
          animation-name: loading;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
:root {
  --hover-color-main: #c00;
  --transition-time: .3s;
}

.bold-row {
  font-weight: bold;
}

.column-15 {
  min-width: 15rem;
}

.column-5 {
  min-width: 5rem;
}

table, td {
  border: 0;
}

table {
  border-collapse: collapse;
}

td {
  -webkit-transition: background-color .3s;
  transition: background-color .3s;
  -webkit-transition: background-color var(--transition-time);
  transition: background-color var(--transition-time);
}

tr:hover td:not(.button-cell) {
  background-color: lavenderblush
}
.modal-window {
  background: white;
  position: absolute;
  border-radius: 3px;
  box-shadow: 0 3px 5px 3px rgba(0,0,0,0.2), 0 3px 6px 0 rgba(0,0,0,0.19);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
  left: 5rem;
  padding: 0 1rem 1rem 1rem;
}

.outer-button {
  background-color: white;
  border-radius: .6rem;
  width: 1.2rem;
  height: 1.2rem;
  text-align: center;
  position: absolute;
  right: -1.2rem;
  top: -1.2rem;
  font-weight: bold;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.outer-button:hover {
  background-color: var(--red-color-light);
  color: white;
  cursor: pointer;
}

.inline-block {
  display: inline-block;
}

.small-margin {
  margin: .1rem;
}

.error-line {
  color: red;
  font-style: italic;
}

.dialog-button {
  border: 1px solid var(--blue-color-main);
  border-radius: 3px;
  padding: .4rem;
  cursor: pointer;
  background-color: white;
  color: var(--blue-color-main);
  -webkit-transition: all var(--transition-time);
  transition: all var(--transition-time);
}

.dialog-button[disabled] {
  border: 1px solid var(--grey-color-main);
  cursor: default;
  color: var(--grey-color-main);
}

.dialog-button:not([disabled]):hover {
  color: white;
  background-color: var(--blue-color-main);
}

.column-20 {
  min-width: 20rem;
}

:root {
  --blue-color-main: #00c;
  --grey-color-main: #888;
  --red-color-main: #c00;
  --red-color-light: #e22;
  --red-color-dark: #600;
  --green-color-main: #090;
  --green-color-light: #2a2;
  --green-color-dark: #060;
  --transition-time: .3s;
}

body {
  font-family: Calibri, Verdana, Geneva, Tahoma, sans-serif
}

.margin-medium {
  margin: 1rem;
}
